              b Link:
              a(:href=`routeProject.link || 'javascript:void(0)'`, target='_blank') {{routeProject.link || 'unavailiable'}}
            .tech-area
              h3.tech-title 
                b Technologies Used:
              ul.tech-list
                li(v-for="str in routeProject.stack") {{str}}
            .image-area
              h3.image-title Image:
              img(:src="routeProject.image").image
        .col-xs-12.nav-row
          nav
            router-link(:to="{name:'home'}").nav-brand
              | McCall Alexander
        .col-xs-12.header-col
          h1 Portfolio
        .col-xs-12.search-bg
            multiselect(
              v-model="query", 
              :options="selectOptions", 
              key="909090909",
              :multiple="true",
              :close-on-select="true",
              :clear-on-select="true",
              :hide-selected="true",
              placeholder="Search something!☟",
              :show-labels="false"
              ).multiselect
            
            //- .multiselect(
            //-   v-model="query", 
            //-   :options="selectOptions", 
            //-   key="909090909",
