            //-   placeholder="Search something!☟",
            //-   :show-labels="false"
            //- )
        .col-xs-12.projects-bg
          .row
            .projects.col-xs-10.col-xs-offset-1
              transition-group(name="fade" appear).projects-list
                .project-item(
                  v-for="(project, index) in projects",
                  :key="index"
                )
                  project(
                    :key="index", 
                    :index="index", 
                    :project="project",
                  )
</template>

<script>
  import { SweetModal, SweetModalTab } from 'sweet-modal-vue/dist/sweet-modal.js'
  import Multiselect from 'vue-multiselect'
  import Project from './Project'
  import Modal from './Modal'
  import projects from './projects'
  import Sine from './Sine'
  import isMobile from 'ismobilejs'

  import Fuse from 'fuse.js'


  export default {
    name: 'portfolio',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        scrollOrigin:0,
        modalVisible:false,
        projectList:projects,
        query:[]
      }
    },
    computed:{
      routeProject(){
        let routeProj = this.projectList.find (
          (project)=>{
            return project.title == this.$route.params.name
          }
        )

        return routeProj || projects[0];
      },
      projects(){
        let options = {
          keys:['stack']
        }
        let results = this.projectList;
        this.query.forEach(
          str=>{
            let fuse = new Fuse(results, options);
            results = fuse.search(str)
          }
        )
        return results.sort((a,b)=>{
          return a.weight - b.weight
        }, {weight:0})
       
      },
      isMobile(){
        return isMobile.any
      },
      selectOptions(){
        let fullStack = new Set();
        this.projects.forEach((project)=>{
          project.stack.forEach(item=>{
            fullStack.add(item)
          })
        });
        return [...fullStack]
      },
    },
    components:{
      Project,
      Sine,
      Modal,
      Multiselect,
      SweetModal,
		  SweetModalTab,
    },
    methods:{
      handleScroll(){
        let winHeight = window.innerHeight
        let centerScrollPos = window.scrollY - winHeight/2
        this.scrollOrigin = centerScrollPos
      },
      showModal(){
        this.modalVisible = true;
      },
      hideModal(){
        this.$router.push({name:'portfolio'})
      }
    },
    mounted(){
      console.log(projects)
      let vm = this;
      let ecount = 0;
      // window.addEventListener('scroll', this.handleScroll);
      // this.handleScroll()
    },
    beforeDestroy () {
      let a = 0
    }
  }
</script>

